.nav-bar{
	display: flex;
	width: 95%;
	padding: 24px ;
	align-items: center;
	gap: 145px;
	border-bottom: 1px solid var(--velosaty-action-secondary-40, #9BA2AE);
	background: var(--surface-surface-primary, white);

		

}

.nav-text{
	display: flex;
	align-items: flex-start;
	gap: 10px;
	font-weight: bold;
    padding: 5px;
}

.image{
	display: center;
	width: 331px;
	height: 60px;
	align-items: center;
	justify-content: center;

}


/* Media query for smaller screens like smartphones */
@media screen and (max-width: 600px) {
	.nav-bar {
	  flex-direction: column;
	}
  
	.nav-text,
	.logo1 {
	  margin-bottom: 10px; /* Add some space between items in mobile view */
	}
  }
  
  /* Media query for tablets and larger screens */
  @media screen and (min-width: 768px) {
	.nav-bar {
	  gap: 15%; /* Increase the gap between items for larger screens */
	}
  }
