.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.container1 {
  display: flex;
  width: 100%; /* Set container width to full viewport width */
}

.Block1,
.Block2 {
  flex: 1;
  min-height: 500px; /* Use min-height instead of height */
  justify-content: flex-end;
  align-items: center;
  flex-shrink: 0;
}

.Block2 {
  background: #FFD966;
}

.container2 {
  display: flex;
  width: 100%; /* Set container width to full viewport width */
}

.Block3 {
  flex: 1;
  min-height: 500px; /* Use min-height instead of height */
  justify-content: flex-end;
  align-items: center;
  flex-shrink: 0;
}

.goal {
  position: relative; /* Set container as the reference position */
}

.goalBlock2 {
  position: absolute; /* Position the BasicCard component */
  top: 0; /* Align it to the top */
  right: 15%; /* Use relative units for positioning */
}

.goalBlock3 {
  position: absolute; /* Position the BasicCard component */
  top: 0; /* Align it to the top */
  right: 7%; /* Use relative units for positioning */
}

.intern2 {
  position: absolute; /* Position the third BasicCard component */
  top: 67%; /* Use relative units for positioning */
  left: 10%; /* Use relative units for positioning */
}

.intern3 {
  position: absolute; /* Position the fourth BasicCard component */
  top: 56%; /* Use relative units for positioning */
  left: 15%; /* Use relative units for positioning */
}

.intern4 {
  position: absolute; /* Position the third BasicCard component */
  top: 45%; /* Use relative units for positioning */
  left: 10%; /* Use relative units for positioning */
}

.button1,
.button2,
.button3 {
  position: absolute; /* Position the buttons */
  flex: 0;
}

.button1 {
  top: 69%; /* Use relative units for positioning */
  left: 15%; /* Use relative units for positioning */
}

.button2 {
  top: 59%; /* Use relative units for positioning */
  left: 18%; /* Use relative units for positioning */
}

.button3 {
  top: 47%; /* Use relative units for positioning */
  left: 15%; /* Use relative units for positioning */
}

.head {
  position: absolute; /* Position the head element */
  top: 55%; /* Use relative units for positioning */
  left: 45%; /* Use relative units for positioning */
  width: 60%; /* Use relative units for width */
}

.Block3 {
  position: absolute; /* Position the Block3 element */
  top: 65%; /* Use relative units for positioning */
  left: 40%; /* Use relative units for positioning */
}

.Block4 {
  position: absolute; /* Position the Block4 element */
  top: 67%; /* Use relative units for positioning */
  left: 50%; /* Use relative units for positioning */
}
