.layout {
	width: 100%;
	min-height: 100vh; /* Instead of using a fixed height, use a minimum height to accommodate different screens */
	position: relative;
	background: linear-gradient(0deg, rgba(0, 0, 0, 0.40) 0%, rgba(0, 0, 0, 0.40) 100%), url('../../assets/cover.png') center / cover no-repeat;
	display: flex;
	align-items: center;
	justify-content: center;
  }
  
  .text {
	width: 90%; /* Use relative units to adapt to different screen widths */
	max-width: 1536px;
	flex-shrink: 0;
	color: #F7F4F2;
	text-align: center;
	font-family: Poppins;
	font-size: 6vw; /* Use relative units for font size */
	font-style: normal;
	font-weight: 700;
	line-height: 1.2;
	margin-bottom: 5vw; /* Use relative units for margin */
  }
  
  .sub {
	color: var(--surface-surface-primary, #F7F4F2);
	width: 90%; /* Use relative units to adapt to different screen widths */
	max-width: 40%;
	text-align: center;
	font-family: Poppins;
	font-size: 2.5vw; /* Use relative units for font size */
	font-style: normal;
	font-weight: 500;
	line-height: 1.6;
	position: absolute;
	bottom: 10%; /* Use relative units for positioning */
	left: 0;
	right: 0;
	margin: 0 auto;
	width: fit-content;
  }
  
  .butpos {
	bottom: 3%; /* Use relative units for positioning */
	position: absolute;
	/* Add other desired styles such as padding, font size, etc. */
	display: flex;
	width: 90%; /* Use relative units to adapt to different screen widths */
	max-width: 490px;
	padding: 4vw 7vw; /* Use relative units for padding */
	justify-content: center;
	align-items: center;
	gap: 2%; /* Use relative units for gap between items */
	font-weight: bold;
  }
  

  .goals-container {
	background: var(--surface-surface-secondary, #EDE9E7);

	height: 30%;
	color: var(--velosaty-action-primary-120, #254C42);
	text-align: center;
	font-family: Poppins;
	font-size: 30px;
	font-style: normal;
	font-weight: 600;
	line-height: 60px;
	padding: 50px 42px;

	
	
}


